import React from "react";
import "./index.css";

const SideNavBar = ({ close, display }) => {
  let Classes = "side-nav-bar";
  if (display) {
    Classes = "side-nav-bar open";
  }

  return (
    <nav className={Classes}>
      <ul>
        <li onClick={close}>
          <img src={require('../../../assets/icons/baseline_close_black_18dp.png').default} />
        </li>
        <li> Dashboard </li>
        <li> Services</li>
      </ul>
    </nav>
  );
};

export default SideNavBar;