import React from "react";
import Axios from "axios";
import {Link} from 'react-router-dom';

import Tabs from "./Tabs/Tabs";
import "./Panel.css";

import PanelMainSection from './Tabs/Sections/PanelMainSection';
import PanelCategory from './Tabs/Sections/PanelCategory';



class Panel extends React.Component {

  state = {
    isAuth: null
  }

  componentDidMount() {
    Axios.get("http://localhost:3001/isAuth", {
      headers: {
        "x-access-token": localStorage.getItem("token")
      },
    }).then(response => {
      if (response.data.auth === true) {
        this.setState({isAuth: true})
      } else {
        this.setState({isAuth: false})
      }
    })
  }

  renderPanel = () => {
    return (
      <div>
        <h1 className="panel-title">Admin Panel</h1>
        <Link className="logout-button" to="/" onClick={() => localStorage.clear()} >LogOut</Link>
        <Tabs>
          <div label="Main Page">
            <PanelMainSection />
          </div>
          <div label="main">
            <PanelCategory />
          </div>   
        </Tabs>
      </div>
    )   
  }

  render() {
    const render = this.state.isAuth === true ? this.renderPanel() : <div>This is administration area. Turn back? <Link to="/">Main Page</Link></div>
    
    return (
      <div>
        {render}
      </div>
    )
  }
}

export default Panel;