import React from 'react';
import '../../Panel.css';

import {Field, reduxForm} from 'redux-form';

class PanelMainSection extends React.Component {

  renderError = ({error, touched}) => {
    if (touched && error) {
      return <div className="error-message">{error}</div>
    }
  }

  renderInput = ({input, label, meta}) => {
    return (
      <div>
        <label>{label}</label>
        <input {...input} autoComplete="off" />
        {this.renderError(meta)}
      </div>
      
    )
  }

  onSubmit(formValues) {
    console.log(formValues)
  }


  render() {
    return (
      <form onSubmit={this.props.handleSubmit(this.onSubmit)} className="panel-form">
        <Field name="title" component={this.renderInput} label="Enter title" />
        <Field name="image" component={this.renderInput} label="Enter image link" />
        <button>Submit</button>
      </form>
    )
  }
}

const validate = (formValues) => {
  const errors = {};

  if (!formValues.title) {
    errors.title = 'You must enter a title';
  }
  if (!formValues.image) {
    errors.image = 'You must enter a image path'
  }
  return errors
}

export default reduxForm({
  form: 'CategoryCreate',
  validate
})(PanelMainSection);